<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Mutasi Stok</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="mutstoks" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["id"]'>
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["Tanggal"] }}</td>
                                <td class="title">{{ item["Kode Barang"] }}</td>
                                <td class="title">{{ item["No Suplier"] }}</td>
                                <td class="title">{{ item["No Bukti"] }}</td>
                                <td class="title">{{ item["Keterangan"] }}</td>
                                <td class="title">{{ item["Masuk"] }}</td>
                                <td class="title">{{ item["Keluar"] }}</td>
                            </tr>
                        </tbody>
                    </template>
                    
                </v-data-table>
            </v-container>
        </v-card>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                dialog: false,
                keyword: '',
                headers: [{
                        text: 'No',
                        value: 'no',
                        class: 'title',
                    },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal',
                        class: 'title',
                    },
                    {
                        text: 'Kode Barang',
                        value: 'Kode Barang',
                        class: 'title',
                    },
                    {
                        text: 'No Suplier',
                        value: 'No Suplier',
                        class: 'title',
                    },
                    {
                        text: 'No Bukti',
                        value: 'No Bukti',
                        class: 'title',
                    },
                    {
                        text: 'Keterangan',
                        value: 'Keterangan',
                        class: 'title',
                    },
                    {
                        text: 'Masuk',
                        value: 'Masuk',
                        class: 'title',
                    },
                    {
                        text: 'Keluar',
                        value: 'Keluar',
                        class: 'title',
                    },
                ],
                mutstoks: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                form: {
                    keterangan: '',
                    kodeUji: '',
                },
                param: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'mutstoks'
                this.$http.get(uri).then(response => {
                    this.mutstoks = response.data
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>